import React from 'react'
import "./Card.css"
import PropTypes from 'prop-types'


const Card = ( {lang, imagenSrc, scolor, fcolor} ) => {
    return (
        <div className="card m-2" style={
            {   width: "16rem",
                border: "1px solid #333333 !important",
                textAlign: "center",
                background: `linear-gradient( to left, ${scolor}, ${fcolor})`, //Template string
                color: "white",
                textShadow: "2px 2px black"
            }}>
            <img src={imagenSrc} className="card-img-top m-auto" alt="languaje IMG"></img>
            <div className="card-body">
                <h5 className="card-title">{lang}</h5>
                <p className="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
                <a href="#" className="btn btn-primary">Go somewhere</a>
            </div>
        </div>        
    )
}

Card.propTypes = {
    lang: PropTypes.string,
    imagenSrc: PropTypes.string,
    scolor: PropTypes.string,
    fcolor: PropTypes.string
}

export default Card
