import React from 'react'
import Card from './Card'

const Cards = () => {

    const dataCards = [
        {
          "lang": "Python",
          "url": "https://cdn.svgporn.com/logos/python.svg",
          "fcolor": "#00c3ff",
          "scolor": "#ffff1c"
        },
        {
          "lang": "Java",
          "url": "https://cdn.svgporn.com/logos/java.svg",
          "fcolor": "#D31027",
          "scolor": "#EA384D"
        },
        {
          "lang": "React",
          "url": "https://cdn.svgporn.com/logos/react.svg",
          "fcolor": "#000428",
          "scolor": "#004e92"
        },
        {
          "lang": "Perl",
          "url": "https://cdn.svgporn.com/logos/perl.svg",
          "fcolor": "#cc2b5e",
          "scolor": "#753a88"
        }
    ];      

    return (
        <div className="d-flex flex-wrap bg-dark justify-content-around">
            
            { //Inicio codigo de JS
            dataCards.map(( {lang, url, scolor, fcolor} ) => { //Us√© DESESTRUCTURACION de los objetos del array
                return ( <Card key={lang} lang={lang} imagenSrc={url} scolor={scolor} fcolor={fcolor} /> );
            })
            //Finalizo codigo de JS
            }
        
        </div>
    )
}

export default Cards
